fff;\n  --card-shadow-hover: rgba(0, 0, 0, 0.1);\n  --temp-hourly: #888;\n  --temp-primary: #2196f3;\n  --forecast-today-bg: rgba(33, 150, 243, 0.1);\n  --forecast-today-border: rgba(33, 150, 243, 0.2);\n  --forecast-high: #333;\n  --forecast-low: #666;\n  --delete-btn: #999;\n  --delete-btn-hover: #e74c3c;\n  --modal-overlay: rgba(0, 0, 0, 0.5);\n  --modal-shadow: rgba(0, 0, 0, 0.3);\n  --modal-text: #666;\n  --modal-confirm: #e74c3c;\n  --modal-confirm-hover: #c0392b;\n  --modal-cancel: #95a5a6;\n  --modal-cancel-hover: #7f8c8d;\n  --detail-border: rgba(0, 0, 0, 0.08);\n  --delete-btn-shadow: rgba(231, 76, 60, 0.3);\n}\n\n@font-face {\n  font-family: 'Lufga Medium';\n  font-style: normal;\n  font-weight: normal;\n  src: local('Lufga Medium'), url('/fonts/LufgaMedium.woff') format('woff');\n}\n\nhtml {\n  font-size: 16px;\n  line-height: 1.5;\n  font-family: var(--font-sans);\n  background-color: var(--color-bg);\n  color: var(--color-text);\n}\n\nbody {\n  min-height: 100vh;\n  display: flex;\n  flex-direction: column;\n  align-items: stretch;\n  padding: 0;\n  background-color: var(--color-bg);\n}\n\n.container {\n  width: 100%;\n  max-width: var(--max-width);\n  margin: 0 auto;\n  padding: 0 1rem;\n}\n\n.text-center {\n  text-align: center;\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  font-family: var(--font-sans);\n  margin-bottom: 0.5em;\n  color: var(--color-accent);\n}\n\na {\n  color: var(--color-accent);\n  text-decoration: none;\n}\n\na:hover {\n  text-decoration: underline;\n}\n\nbutton {\n  font: inherit;\n  cursor: pointer;\n  background-color: var(--color-accent);\n  color: var(--button-text);\n  border: none;\n  padding: 0.5em 1em;\n  border-radius: 4px;\n}\n\nbutton:disabled {\n  opacity: 0.6;\n  cursor: not-allowed;\n}\n\ninput,\ntextarea {\n  font: inherit;\n  padding: 0.5em;\n  border: 1px solid var(--input-border);\n  border-radius: 4px;\n}\n\ninput:focus,\ntextarea:focus {\n  outline: none;\n  border-color: var(--color-accent);\n  box-shadow: 0 0 0 2px rgba(0, 122, 204, 0.2);\n}\n\nul {\n  list-style: none;\n}\n\n/* Navigation header styling */\n.nav-header {\n  width: 100%;\n  background-color: var(--card-bg);\n  box-shadow: 0 2px 8px var(--card-shadow);\n  position: sticky;\n  top: 0;\n  z-index: 100;\n  margin-bottom: 30px;\n}\n\n.nav-container {\n  max-width: 1400px;\n  margin: 0 auto;\n  padding: 15px 20px;\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  gap: 20px;\n}\n\n.nav-logo {\n  flex-shrink: 0;\n}\n\n#header-logo {\n  font-family: 'Lufga Medium', sans-serif;\n  font-size: 2.5rem;\n  font-weight: normal;\n  color: var(--color-accent);\n  margin: 0;\n  letter-spacing: 0.5px;\n}\n\n.nav-controls {\n  display: flex;\n  align-items: center;\n  gap: 12px;\n  flex-shrink: 0;\n}\n\n.nav-controls input {\n  min-width: 200px;\n  max-width: 300px;\n  padding: 8px 12px;\n  border: 1px solid var(--input-border);\n  border-radius: 4px;\n  font-size: 0.9rem;\n}\n\n.nav-controls button {\n  padding: 8px 16px;\n  background-color: var(--color-accent);\n  color: var(--button-text);\n  border: none;\n  border-radius: 4px;\n  cursor: pointer;\n  font-size: 0.9rem;\n  white-space: nowrap;\n  transition: background-color 0.2s ease;\n}\n\n.nav-controls button:hover {\n  opacity: 0.9;\n}\n\n/* Main content area */\n.main-content {\n  flex: 1;\n  width: 100%;\n}\n\n/* Card grid layout */\n#city-cards-container {\n  display: grid;\n  grid-template-columns: repeat(auto-fit, 350px);\n  gap: 20px;\n  padding: 0 20px 40px 20px;\n  max-width: 1400px;\n  margin: 0 auto;\n  width: 100%;\n  justify-content: center;\n}\n\n.city-card {\n  width: 350px;\n  height: 500px;\n  border-radius: 15px;\n  box-shadow: 0 4px 15px var(--card-shadow-hover);\n  overflow: hidden;\n  cursor: pointer;\n  transition: transform 0.2s ease, box-shadow 0.2s ease;\n  position: relative;\n  perspective: 1000px;\n  /* Drag and drop properties */\n  user-select: none;\n}\n\n.city-card[draggable='true'] {\n  cursor: grab;\n  user-select: none;\n}\n\n.city-card[draggable='true']:active,\n.city-card.dragging {\n  cursor: grabbing;\n}\n\n.city-card.dragging {\n  opacity: 0.5;\n  transform: rotate(3deg) scale(1.05);\n  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.3);\n  z-index: 1000;\n  transition: none;\n}\n\n.city-card.drag-over {\n  transform: translateY(-3px) scale(1.01);\n  box-shadow: 0 6px 20px rgba(0, 122, 204, 0.25);\n  border: 2px solid var(--color-accent);\n  transition: all 0.15s ease;\n}\n\n.city-card.drag-adjacent {\n  transform: translateY(-1px);\n  box-shadow: 0 4px 15px rgba(0, 122, 204, 0.15);\n  border: 1px solid rgba(0, 122, 204, 0.3);\n  transition: all 0.2s ease;\n}\n\n.drag-placeholder {\n  width: 350px;\n  height: 500px;\n  border: 4px dashed var(--color-accent);\n  border-radius: 15px;\n  background: linear-gradient(\n    135deg,\n    rgba(0, 122, 204, 0.15),\n    rgba(0, 122, 204, 0.08)\n  );\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  color: var(--color-accent);\n  font-size: 1.1rem;\n  font-weight: 700;\n  opacity: 0.9;\n  transition: all 0.3s ease;\n  animation: placeholderPulse 1.2s ease-in-out infinite;\n  box-sizing: border-box;\n  position: relative;\n}\n\n@keyframes placeholderPulse {\n  0%,\n  100% {\n    opacity: 0.9;\n    transform: scale(1);\n    border-color: var(--color-accent);\n  }\n  50% {\n    opacity: 0.7;\n    transform: scale(0.98);\n    border-color: rgba(0, 122, 204, 0.7);\n  }\n}\n\n.drag-placeholder::before {\n  content: 'Drop here';\n  z-index: 1;\n}\n\n.drag-placeholder::after {\n  content: '';\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  width: 60px;\n  height: 60px;\n  border: 3px solid var(--color-accent);\n  border-radius: 50%;\n  transform: translate(-50%, -50%);\n  opacity: 0.6;\n}\n\n.city-card:hover:not(.dragging) {\n  transform: translateY(-2px);\n}\n\n.city-card[draggable='true']:hover:not(.dragging) {\n  transform: translateY(-3px) scale(1.01);\n  box-shadow: 0 6px 20px var(--card-shadow-hover);\n}\